<script context="module">
  import { snackList } from '../stores/main'
  import { urlData } from '../stores/router'
  import { removeProject } from '../api/project'

  let canRemove = false

  export const title = 'Excluir Projeto'
  export const actions = [
    {
      label: 'Sim',
      order: -1,
      callback: () => {
        if (canRemove) {
          removeProject(canRemove).then(data => {
            urlData.set({
              segment: 'projects',
              query: {},
            })
          })
          return true
        } else
          snackList.add({
            text: 'Campo de confirmação não foi preenchido ou não combina!',
          })
      },
    },
  ]
</script>

<script>
  import { selectTextContent } from './utils'
  import Input from './Input.svelte'

  // Stores
  import { currentProject } from '../stores/project'

  let code = ''

  $: if (code === $currentProject.slug) {
    canRemove = $currentProject.slug
  } else {
    canRemove = false
  }
</script>

<style>
  span {
    color: red;
  }

  .red {
    color: red;
  }

  .green {
    color: green;
  }

  div {
    display: flex;
    align-items: center;
  }

  p.type-body2 {
    font-size: 14px;
  }
</style>

<p class="type-body1">
  Se você tem certeza de que quer remover o projeto
  <span>{$currentProject.name}</span>
  , redigite o codigo
  <span
    on:click={evt => selectTextContent(evt.target)}
    title="click para selecionar">
    {$currentProject.slug}
  </span>
  na caixa de texto abaixo e confirme.
</p>
<p class="type-body2">
  <b>⚠ ATENÇÃO!</b>
  a exclusão do projeto é inreversivel! Só confirme se tiver absoluta ceteza!
</p>
<div>
  <Input bind:value={code} label="Ex: Bw93feGh42oHhekC" />
  {#if !!code && code !== $currentProject.slug}
    <i class="material-icons red">close</i>
  {/if}
  {#if !!code && code === $currentProject.slug}
    <i class="material-icons green">checked</i>
  {/if}
</div>
