<script>
  import { createEventDispatcher } from 'svelte'
  import { ripple } from 'fluido-components'
  import { scale } from 'svelte/transition'
  import { preventEvent } from './utils'

  export let node = null
  export let icon = 'add'

  let eventType = null

  const dispatch = createEventDispatcher()

  function clear() {
    setTimeout(() => {
      eventType = null
    }, 1000)
  }

  function handleClick(evt) {
    if (!eventType) eventType = 'click'
    if (eventType !== 'click') return preventEvent(evt)

    dispatch('click', { target: evt.target })
    clear()
    return preventEvent(evt)
  }

  function handleTouch(evt) {
    if (!eventType) eventType = 'touch'
    if (eventType !== 'touch') return

    dispatch('click', { target: evt.target })
    clear()
  }
</script>

<style>
  button {
    height: 48px;
    width: 48px;
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 100%;
    grid-template-rows: 100%;
    color: inherit;
    cursor: pointer;
    background-color: transparent;
    border: none;
    border-radius: 1000000px;
    outline: none;
    position: relative;
  }

  button:focus::after,
  button:active::after,
  button:hover::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 1000000px;
    background-color: currentColor;
    opacity: 0.12;
  }

  i {
    grid-column: 1;
    grid-row: 1;
    line-height: 100%;
    text-align: center;
  }
</style>

<button
  use:ripple={{ center: true }}
  on:click={handleClick}
  on:touchstart={preventEvent}
  on:touchend={handleTouch}
  on:mousedown={preventEvent}
  bind:this={node}
  class="noselect">
  {#each [icon] as ic (ic)}
    <i transition:scale class="material-icons">{ic}</i>
  {/each}
</button>
